	module.exports = {"data":{"nimblRenderedHtml":{"html":"<h1 id=\"schopenhauer\">Schopenhauer</h1>\n<p>L'opera principale di Schopenhauer è &quot;Il mondo come volontà e rappresentazione&quot;. Egli vuole conciliare visioni contrastanti su Platone, Kant, Veda e Upanishad (testi religiosi induisti e buddisti).<br>\nDal punto di vista conoscitivo ha ragione Kant: la realtà può essere compresa solo come rappresentazione, ma possiamo anche intuire il mondo attraverso la <strong>volontà</strong>, perché abbiamo un corpo desiderante.<br>\nLa verità è quindi coperta dal &quot;velo di Maja&quot; dei fenomeni e delle rappresentazioni che ci impedisce di capire che la vera radice del mondo è volontà.</p>\n<p><strong>KANT E SCHOPENHAUER</strong>: Schopenhauer viola il principio Kantiano dell'impossibilità di conoscere la cosa in sè.</p>\n<p><strong>HEGEL E SCHOPENHAUER</strong>: Per Hegel la vera natura del mondo è razionalità, per Schopenhauer invece è la volontà.</p>\n<p><strong>VOLONTÀ E RAPPRESENTAZIONE</strong>: Le rappresentazioni sono soggette alle forme a priori di spazio e tempo, sono molteplici e rispettano il principio di causalità. La volontà invece è una <strong>unica e senza un fine</strong>.<br>\nLa volontà non è soddisfacibile, è un infinito tendere. Il mondo è quindi infelice e assurdo.</p>\n<p><strong>ARTE, COMPASSIONE E ASCETISMO</strong>: La migliore condizione per l'uomo è l'assenza di volontà, che possiamo raggiungere per 3 vie:</p>\n<ul>\n<li>contemplazione dell'arte, che però è una soluzione temporanea, dato che ad un certo punto i nostri bisogni avranno il sopravvento (fame, sete, ...)</li>\n<li>compassione, perché mentre aiuto gli altri non mi curo dei miei desideri inoltre riusciamo ad infrangere il velo di Maja e superare la nostra individualità</li>\n<li>ascetismo, è la soluzione migliore, perché ci permette di superare i nostri bisogni definitivamente</li>\n</ul>\n<p><strong>SCHOPENHAUER E DAWKINS</strong> Dawkins ne &quot;Il gene egoista&quot;sostiene che il DNA si riproduce utilizzando l'uomo come mezzo. L'uomo non è quindi soggetto ma oggetto dell'evoluzione. L'uomo si ritiene unico e speciale, ma l'evoluzione mostra che in realtà è il prodotto casuale dell'evoluzione.<br>\nIl ruolo dell'evoluzione in Schopenhauer è assunto dalla volontà.<br>\nDawkins propone anche l'idea dei memi, equivalenti culturali dei geni: i memi sono idee che si trasmettono di persona in persona, usando l'uomo come mezzo.</p>\n<p><strong>SCHOPENHAUER SUL SUICIDIO</strong> Il suicidio è insensato, perché è un atto di volontà e perché anche se il singolo individuo muore la volontà è inalterata, dato che essa è oltre il concetto di molteplicità.</p>\n","frontmatter":{"title":"Schopenhauer","pdfCompletoPath":"filo-schopenhauer.pdf","draft":false},"headings":[{"value":"<p>Schopenhauer</p>\n","depth":1,"rawText":"Schopenhauer\r"}]}},"pathContext":{"category":"Filosofia","title":"Schopenhauer"}}

